import React from "react"
import { Link } from "gatsby"
import styled from "styled-components"

import Title from "../atoms/Title"
import Divider from "../atoms/Divider"
import { scale, rhythm } from "../../utils/typography"

export default class Navigation extends React.Component {
  getLinkList() {
    let activeType = this.props.active
    let getClassName = active => (active ? "is-active" : "")
    let res = (
      <Menu className="manu">
        <ul className="menu-list">
          <li>
            <Link className={getClassName(activeType === "Home")} to="/">
              Home
            </Link>
          </li>
          <li>
            <Link className={getClassName(activeType === "Blog")} to="/blog/">
              Blog
            </Link>
          </li>
          <li>
            <Link className={getClassName(activeType === "About")} to="/about/">
              About
            </Link>
          </li>
          <li>
            <Link
              className={getClassName(activeType === "Contact")}
              to="/contact/"
            >
              Contact
            </Link>
          </li>
        </ul>
      </Menu>
    )
    return res
  }

  getSNSLink() {
    return (
      <SNS>
        <span class="icon is-medium">
          <a href="https://github.com/DaichiTakigawa">
            <i class="fab fa-lg fa-github"></i>
          </a>
        </span>
        <span class="icon is-medium">
          <a href="https://twitter.com/damenin27844103">
            <i class="fab fa-lg fa-twitter"></i>
          </a>
        </span>
      </SNS>
    )
  }

  render() {
    return (
      <>
        <Title />
        <StyledSticky>
          <StyledDivider />
          {this.getLinkList()}
          {this.getSNSLink()}
        </StyledSticky>
      </>
    )
  }
}

const StyledDivider = styled(Divider)`
  margin: ${rhythm(2)} ${rhythm(1)};
`

const Menu = styled.nav`
  margin: ${rhythm(2)} ${rhythm(1)};
`

const SNS = styled.div`
  margin: ${rhythm(2)} ${rhythm(1)};
`

const StyledSticky = styled.div`
  position: sticky;
  top 0;
`
